<template>
  <div class="progress-container flex-center">
    <h4 class="title">
      {{ $t('model.info.download_excel') }}
    </h4>
    <el-progress
      type="circle"
      :stroke-width="10"
      :width="240"
      :color="colors"
      :percentage="percentage" />
  </div>
</template>

<script>
export default {
  props: {
    vm: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      percentage: 0,
      radio: 0.025,
      timer: null,
      colors: [
        { color: '#f56c6c', percentage: 20 },
        { color: '#e6a23c', percentage: 40 },
        { color: '#5cb87a', percentage: 60 },
        { color: '#1989fa', percentage: 80 },
        { color: '#6f7ad3', percentage: 100 },
      ],
    };
  },
  mounted() {
    // 开始读取下载速度条
    this.startProgress();

    // 监听下载完成事件
    this.vm.$on('downloaded', this.completeProcess);
  },
  methods: {
    // 模拟下载速度
    startProgress() {
      let total = 1;
      let speed = 0;

      this.timer = setInterval(() => {
        speed = (100 - total) * this.radio;
        total += speed;
        this.percentage = Math.round(total);
        if (this.percentage >= 100) {
          this.percentage = 100;
          clearInterval(this.timer);
          this.$emit('close');
        }
      }, 250);
    },
    // 加快进度条读取速度
    completeProcess() {
      if (this.percentage <= 30) {
        this.radio = 0.8;
      } else if (this.percentage <= 60) {
        this.radio = 0.9;
      }
    },
  },
};
</script>

