<template>
  <div class="detail-text">
    <form-field-text
      :label="$t('model.info.style_name')"
      :content="model.modelNumber"
    />
    <form-field-text
      :label="$t('model.info.type')"
      :content="model.typeName && model.typeName.label"
    />
    <form-field-text
      :label="$t('model.info.notes')"
      :content="model.notes"
      :ellipsis="false"
    />

    <!-- 标签 -->
    <form-field-text
      :label="$t('model.info.tags')"
      :content="model.tags.length"
    >
      <ul class="tags-list">
        <li v-for="(item, index) in model.tags" :key="index" class="el-tag">
          {{ item.tag }}
        </li>
      </ul>
    </form-field-text>

    <ul class="section-half">
      <li
        v-for="field in generateFields()"
        :key="field.label"
        class="form-field"
      >
        <form-field-text
          :label="field.label"
          :content="field.content || ''"
          :unit="field.unit"
        />
      </li>
    </ul>
  </div>
</template>

<script>
import i18n from 'i18next';

import FormFieldText from '@/components/form/FormFieldText.vue';
import { date } from '@/filters';

export default {
  name: 'ModelDetailText',
  components: {
    FormFieldText,
  },
  props: {
    model: {
      type: Object,
      required: true,
    },
  },
  methods: {
    generateFields() {
      const {
        season, series, lastNumber, customer, soleNumber,
        designerName, heelNumber, price, monetaryUnit, style, category,
        waveband, gender, designerLeader, craftsman, colour, size, factory,
        purpose, dueTime,
      } = this.model;

      const genderList = [i18n.t('material.info.men'), i18n.t('material.info.women'), i18n.t('material.info.children')];

      return [
        { label: i18n.t('model.info.style'), content: style },
        { label: i18n.t('model.info.category'), content: category },
        { label: i18n.t('model.info.waveband'), content: waveband },
        { label: i18n.t('model.info.gender'), content: gender ? genderList[Number(gender) - 1] : '' },
        { label: i18n.t('model.info.season'), content: season },
        { label: i18n.t('model.info.collection'), content: series },
        { label: i18n.t('model.info.last_number'), content: lastNumber },
        { label: i18n.t('model.info.outsole_number'), content: soleNumber },
        { label: i18n.t('model.info.heel_number'), content: heelNumber },
        { label: i18n.t('model.info.customer'), content: customer },
        { label: i18n.t('model.info.designerLeader'), content: designerLeader },
        { label: i18n.t('model.info.designer'), content: designerName },
        { label: i18n.t('model.info.technician'), content: craftsman },
        { label: i18n.t('model.info.colour'), content: colour },
        { label: i18n.t('model.info.price'), content: price, unit: monetaryUnit },
        { label: i18n.t('model.info.size'), content: size },
        { label: i18n.t('model.info.factory'), content: factory },
        { label: i18n.t('model.info.purpose'), content: purpose },
        { label: i18n.t('model.info.dueDate'), content: date(dueTime, 'MM/DD/YYYY') },
      ];
    },
  },
};
</script>

