.tags-list {
  min-height: 30px;
  margin-top: 10px;

  .el-tag {
    height: 30px;
    padding: 0 18px;
    margin: 0 10px 10px 0;
    font-size: 14px;
    line-height: 30px;
    background-color: #fff;
    border-radius: 15px;
  }
}
