<template>
  <div class="model-info-container flex-column flex-1">
    <!-- 弹框标题 -->
    <h1 class="dialog-title no-collapse">
      {{ $t("model.info.design_info") }}
    </h1>

    <div class="model-content-box flex-1 scrollable-y ">
      <!-- 名称和类型 -->
      <model-detail-text v-if="mode === 'text'" :model="model" />
      <model-detail-form v-else :model="modelFormData" ref="form" />

      <!-- 材质表格 -->
      <section>
        <h2 class="section-title highlight">
          {{ $t("model.info.materials") }}
        </h2>
        <material-table :data="model.materials" />
      </section>

      <!-- 鞋上传和修改时间 -->
      <model-time-info
        :creator="model.creatorName"
        :uploaded="model.uploadTime"
        :updated="model.lastTime"
        style="margin-top: 20px"
      />
    </div>

    <!-- 取消和确认按钮 -->
    <section class="submit-button-area">
      <!-- <div v-if="mode === 'text'" class="flex-space-between"> -->
      <div v-if="mode === 'text'" class="flex-justify-flex-end">
        <!--
        <el-button
          :disabled="!$can('workspace', 'OP0013')"
          type="primary"
          @click="$emit('foreward')">
          Export the sample list
        </el-button>
        -->
        <el-button
          :disabled="(!isCreator && !$can('workspace', 'OP0015')) || editable === false"
          type="primary"
          @click="toggleComponent('form')"
        >
          <i class="iconfont icon-write" />
          <span>
            {{ $t("model.info.edit") }}
          </span>
        </el-button>
      </div>
      <div v-else class="text-align-right">
        <el-button @click="toggleComponent('text')">
          {{ $t("public.dialog.cancel") }}
        </el-button>
        <el-button type="primary" @click.native="updateInfo">
          {{ $t("public.dialog.confirm") }}
        </el-button>
      </div>
    </section>
  </div>
</template>

<script>
import Vue from 'vue';

import ModelTimeInfo from '@/views/material/info/components/TimeInfo';
import { getContextUser } from '../../../../loaders/context';
import ModelDetailForm from './model-info/DetailForm';
import ModelDetailText from './model-info/DetailText';
import MaterialTable from './model-info/MaterialTable';
// import Scrollable from '@/components/Scrollable.vue';

export default Vue.extend({
  name: 'ComponentModelInfo',
  components: {
    ModelDetailForm,
    ModelDetailText,
    MaterialTable,
    ModelTimeInfo,
  },
  props: {
    model: {
      type: Object,
      required: true,
    },
    editable: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      mode: 'text', // mode: form, text
      modelFormData: {},
    };
  },
  computed: {
    isCreator() {
      return getContextUser()?.id === this.model.creator;
    },
  },
  methods: {
    toggleComponent(compName) {
      if (compName === 'text') this.$emit('refresh');
      this.mode = compName;
    },

    async updateInfo() {
      await this.$refs.form.changeModelInfo();
      this.toggleComponent('text');
    },
  },
  watch: {
    model: {
      handler(newV) {
        this.modelFormData = newV;
      },
      immediately: true,
    },
  },
});
</script>

