<template>
  <!-- 名称和类型 -->
  <el-form :model="model" :rules="rules" size="medium" ref="form">
    <section class="form-section clearfix">
      <el-form-item prop="modelNumber" :label="$t('model.info.style_name')">
        <el-input
          v-model="model.modelNumber"
          :disabled="!hasPermission('OP0015', 'OP0141')"
          :placeholder="$t('model.info.input_placeholder')" />
      </el-form-item>

      <el-form-item prop="type" :label="$t('model.info.type')">
        <dictionary-selector
          :value="model.type"
          :disabled="!isCreator"
          type="shoeCategory"
          :placeholder="$t('model.info.shoe_category')"
          class="width-100"
          @on-change="changeProperty('type', $event)" />
      </el-form-item>
    </section>

    <!-- 鞋标签 -->
    <section class="form-section clearfix" style="margin: 0 0 5px;">
      <h2 class="section-title">
        {{ $t('model.info.tags') }}
      </h2>
      <TagsEditor :tags="model.tags"
        :shareId="model.id" :type="0" :relateType="0" :editable="hasPermission('OP0015' ,'OP0141')" />
    </section>

    <!-- 备注信息 -->
    <section class="form-section clearfix" style="margin-right: 0px;">
      <h2 class="section-title">
        {{ $t('model.info.notes') }}
      </h2>
      <el-input
        v-model="model.notes"
        :disabled="!hasPermission('OP0015', 'OP0141')"
        :placeholder="$t('model.info.input_placeholder')"
        :rows="3"
        type="textarea"
        resize="none"
        clearable
        maxlength="200"
        show-word-limit />
    </section>

    <!-- 鞋基本信息 -->
    <section class="form-section clearfix">
      <el-form-item
        v-for="item in basicList"
        :key="item.label"
        :label="item.label"
        :prop="item.prop">
        <el-select
          v-if="item.prop === 'gender'"
          v-model="model[item.prop]"
          :disabled="!isCreator"
          :placeholder="$t('model.info.input_placeholder')">
          <el-option label="男" value="1"></el-option>
          <el-option label="女" value="2"></el-option>
          <el-option label="童" value="3"></el-option>
        </el-select>
        <input-with-unit
          v-else-if="item.prop === 'price'"
          :value="model.price"
          :disabled="!isCreator"
          :default-unit="model.monetaryUnit"
          :unit-options="[
            { value: 'USD', label: $i18n.i18next.language === 'en' ? 'USD' : '美元' },
            { value: 'CNY', label: $i18n.i18next.language === 'en' ? 'CNY' : '人民币' }
          ]"
          :inputPlaceholder="$t('model.info.input_placeholder')"
          @on-input-blur="changeProperty('price', $event)"
          @on-select-change="changeProperty('monetaryUnit', $event)" />

        <el-date-picker
          v-else-if="item.prop === 'dueTime'"
          v-model="model[item.prop]"
          format="MM/dd/yyyy"
          value-format="timestamp"
          type="date"
          size="small"
          style="width: 100%"
          @change="changeModelInfo('dueTime', (new Date($event)).valueOf())" />

        <el-input
          v-else
          v-model="model[item.prop]"
          :disabled="!isCreator"
          :placeholder="$t('model.info.input_placeholder')" />
      </el-form-item>

      <!-- <el-form-item prop="price" :label="$t('model.info.prices')">
        <input-with-unit
          :value="model.price"
          :disabled="!isCreator"
          :default-unit="model.monetaryUnit"
          :unit-options="[
            { value: 'USD', label: $i18n.i18next.language === 'en' ? 'USD' : '美元' },
            { value: 'CNY', label: $i18n.i18next.language === 'en' ? 'CNY' : '人民币' }
          ]"
          :inputPlaceholder="$t('model.info.input_placeholder')"
          @on-input-blur="changeProperty('price', $event)"
          @on-select-change="changeProperty('monetaryUnit', $event)" />
      </el-form-item> -->
    </section>
  </el-form>
</template>

<script>
import Vue from 'vue';

import i18n from 'i18next';
import AppError from '@/utils/error';
import DictionarySelector from '@/components/dictionary/DictionarySelector';
import InputWithUnit from '@/components/form/InputWithUnit';
import { updateModelInfo } from '@/api/shoe';
import formRules from '@/views/model/info/rules';
import TagsEditor from '@/components/base/TagsEditor.vue';
import mixinHasPermission from '../../mixinHasPermission';
import { getContextUser } from '../../../../../loaders/context';

export default Vue.extend({
  name: 'ModelDetailForm',
  components: {
    TagsEditor,
    DictionarySelector,
    InputWithUnit,
  },
  props: {
    model: {
      type: Object,
      required: true,
    },
  },
  mixins: [mixinHasPermission],
  data() {
    return {
      rules: formRules,
      basicList: [
        { label: i18n.t('model.info.itemNumber'), prop: 'style' },
        { label: i18n.t('model.info.category'), prop: 'category' },
        { label: i18n.t('model.info.frequency'), prop: 'waveband' },
        { label: i18n.t('model.info.gender'), prop: 'gender' },
        { label: i18n.t('model.info.season'), prop: 'season' },
        { label: i18n.t('model.info.collection'), prop: 'series' },
        { label: i18n.t('model.info.last_number'), prop: 'lastNumber' },
        { label: i18n.t('model.info.outsole_number'), prop: 'soleNumber' },
        { label: i18n.t('model.info.heel_number'), prop: 'heelNumber' },
        { label: i18n.t('model.info.customer'), prop: 'customer' },
        { label: i18n.t('model.info.designerLeader'), prop: 'designerLeader' },
        { label: i18n.t('model.info.designer'), prop: 'designerName' },
        { label: i18n.t('model.info.technician'), prop: 'craftsman' },
        { label: i18n.t('model.info.colour'), prop: 'colour' },
        { label: i18n.t('model.info.price'), prop: 'price' },
        { label: i18n.t('model.info.size'), prop: 'size' },
        { label: i18n.t('model.info.factory'), prop: 'factory' },
        { label: i18n.t('model.info.purpose'), prop: 'purpose' },
        { label: i18n.t('model.info.dueDate'), prop: 'dueTime' },
      ],
    };
  },
  computed: {
    isCreator() {
      return getContextUser().id === this.model.creator;
    },
  },
  methods: {
    // 修改鞋模型信息
    async changeModelInfo() {
      const groupAppId = this.$closest('ModelInfo').groupAppId || '';
      // 先校验填写内容是否通过表单校验
      const valid = await this.$refs.form.validate();
      if (!valid) {
        throw AppError.abort;
      }

      // 发出更新数据请求
      try {
        await updateModelInfo({ relateId: this.model.id, ...this.model }, groupAppId);
      } catch (err) {
        this.$errHandler(err);
      }
    },
    // 处理其他组件更新的事件
    changeProperty(prop, value) {
      // 先要把数据同步到当前的model，否则表单对当前属性检验是旧数据
      this.model[prop] = value;
      // this.changeModelInfo(prop, value);
    },
  },
});
</script>

