.el-form {
  .form-section {
    margin-right: -20px;
    margin-bottom: 15px;

    .el-form-item {
      float: left;
      width: 300px;
      margin-right: 15px;
      margin-bottom: 0;
    }
  }
  .el-select {
    width: 100%;
  }

  /deep/ .el-form-item__label {
    float: none;
  }
}

// 公共样式
.section-title {
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 600;
  color: #333;
  .section-subtitle {
    font-weight: 700;
  }
}

/deep/ .input-unit {
  height: 36px;
}
</style>
