<template>
  <div>
    <p class="dialog-subtitle">
      {{ $t('model.info.complete_info_to_download_excel_tip') }}
    </p>

    <el-form
      :model="form"
      :rules="rules"
      label-width="85px"
      size="small"
      class="clearfix"
      ref="form">
      <el-form-item label="Purpose" prop="purpose">
        <el-input
          v-model="form.purpose"
          @change="changeModelInfo('purpose', $event)" />
      </el-form-item>

      <el-form-item label="Due Date" prop="dueDate">
        <el-date-picker
          v-model="form.dueDate"
          format="MM/dd/yyyy"
          type="date"
          size="small"
          style="width: 100%"
          @change="changeModelInfo('dueDate', $event.getTime())" />
      </el-form-item>

      <el-form-item label="Size" prop="size">
        <el-input
          v-model="form.size"
          @change="changeModelInfo('size', $event)" />
      </el-form-item>

      <el-form-item label="Factory" prop="factory">
        <el-input
          v-model="form.factory"
          @change="changeModelInfo('factory', $event)" />
      </el-form-item>

      <el-form-item label="Notes" prop="notes" class="width-100">
        <el-input
          v-model="form.notes"
          type="textarea"
          :rows="4"
          resize="none"
          maxlength="200"
          show-word-limit
          @change="changeModelInfo('notes', $event)" />
      </el-form-item>
    </el-form>

    <div class="text-align-right">
      <el-button @click="$emit('backward')">
        {{ $t('model.info.back') }}
      </el-button>
      <el-button type="primary" @click="$act(downloadExcel)">
        {{ $t('model.info.export') }}
      </el-button>
    </div>
  </div>
</template>

<script>
import { downloadShoeInfoExcel, updateModelInfo } from '@/api/shoe';

import exec from '@/utils/exec';
import formRules from '../rules';

export default {
  props: {
    model: {
      type: Object,
      required: true,
    },
    vm: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      form: {
        id: this.model.id,
        purpose: this.model.purpose,
        dueDate: this.model.dueTime ? new Date(this.model.dueTime) : '',
        size: this.model.size,
        factory: this.model.factory,
        notes: this.model.notes,
      },
      rules: formRules,
    };
  },
  methods: {
    // 修改鞋模型信息
    changeModelInfo(prop, value) {
      // 先校验填写内容是否通过表单校验
      this.$refs.form.validate((valid, invalidFields) => {
        // 不通过表单校验，且当前更新的内容又在表单校验不通过的内容中；
        // 发出更新失败的提示
        if (!valid && Object.keys(invalidFields).includes(prop)) {
          this.$errHandler({ message: 'Update failed' }, -1);
          return;
        }

        // 发出更新数据请求
        try {
          updateModelInfo({ relateId: this.form.id, [prop]: value });
        } catch (err) {
          this.$errHandler(err);
        }
      });
    },
    // 下载Excel
    async downloadExcel() {
      // 跳到下一步
      this.$emit('foreward');

      const { data: url } = await downloadShoeInfoExcel(this.model.id);

      if (/^\/qt\//.test(this.$route.path)) {
        return exec('downloadStaticFile', url);
      }

      // 创建a标签
      this._createDownloadTag(url);
      return undefined;
    },
    // 创建一个 a 标签，将下载地址赋值给 src； 触发点击事件，删除改标签
    _createDownloadTag(url) {
      let domA = document.createElement('a');
      domA.href = url;
      domA.download = 'file';
      domA.target = '__blank';
      domA.click();
      domA = null;
      this.vm.$emit('downloaded');
    },
  },
};
</script>

<style lang="scss" scoped>
.dialog-subtitle {
  margin-bottom: 20px;
  font-size: 15px;
  color: $color-font-dark;
}

.el-form-item {
  float: left;
  width: 50%;
}
</style>
