<template>
  <component
    v-loading="model === loading"
    :is="currentComponent"
    :model="model"
    :vm="vm"
    @backward="switchPreviousComponent"
    @foreward="switchNextComponent"
    @close="$emit('cancel')"
    :editable="editable"
    @refresh="getShoeInfo(modelId)"
  />
</template>

<script>
import Vue from 'vue';
import { defineComponent } from '@vue/composition-api';

import { getResourceInfo } from '@/api/shoe';
import { getUsersInfoByIds } from '@/api/account';
import ComponentModelInfo from './components/ComponentModelInfo';
import ComponentAdditionalInfo from './components/ComponentAdditionalInfo';
import ComponentDownloadProcess from './components/ComponentDownloadProcess';

import modelForm from './form';

export default defineComponent({
  name: 'ModelInfo',
  dialogify: {
    props: {
      width: '692px',
      closeOnClickModal: false,
      flex: 'column',
      top: 'auto',
      bottom: 'auto',
      compact: true,
      maxHeight: '80vh',
      // height: '90vh',
    },
    closeWith: 'model',
  },
  components: {
    ComponentModelInfo,
    ComponentAdditionalInfo,
    ComponentDownloadProcess,
  },
  props: {
    modelId: {
      types: String,
      required: true,
    },
    groupAppId: {
      types: String,
    },
    editable: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      // 组件数组
      components: ['ComponentModelInfo', 'ComponentAdditionalInfo', 'ComponentDownloadProcess'],
      index: 0, // 当前组件的下标
      model: modelForm, // 获取公共的鞋模型属性对象
      loading: true,
      vm: new Vue(),
    };
  },
  computed: {
    currentComponent() {
      return this.components[this.index];
    },
  },
  watch: {
    modelId: {
      handler: 'getShoeInfo',
      immediate: true,
    },
  },
  destroyed() {
    this.vm = null;
  },
  methods: {
    // 跳转到上一步
    switchPreviousComponent() {
      if (this.index > 0) this.index--;
    },
    // 跳转到下一步
    switchNextComponent() {
      if (this.index < 2) this.index++;
    },
    // 获取模型信息
    async getShoeInfo(modelId) {
      try {
        this.loading = true;
        this.model = (await getResourceInfo(modelId))?.data;
        this.usersInfo = (await getUsersInfoByIds([this.model.creator])).data;
        this.model.creatorName = this.usersInfo[0]?.name || '';
      } catch (err) {
        this.$errHandler(err);
      } finally {
        this.loading = false;
      }
    },
  },
});
</script>
