import getGroupAppId from '@/router/utils/get-group-app-id';

export default {
  data() {
    return {
      currentWorkspaceId: getGroupAppId(),
    };
  },
  methods: {
    // 判定是否有权限（包括在自己空间和在workspace空间）
    hasPermission(workspaceCode, systemCode) {
      // 根据 currentWorkspaceId 是否有值，判定是不是在workspace的中
      if (this.currentWorkspaceId) {
        return this.$can(this.currentWorkspaceId, workspaceCode);
      }
      return this.$can('system', systemCode);
    },
  },
};
